/*Page Style*/
html, body{
    width: 100%;
    height: 100%;
    margin:0;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 9pt;
  }
  
  .full-height{
    width: 100%;
    height: 100%;
  }
  
  :focus{
    outline: none;
  }

  
  /*Login Page*/
  .loginheader{
    background: #637A9F;
    height:40px;
    color: white;
    line-height: 30px;
    font-weight: bold;
    font-size: 17pt;
    text-align: center;
  }
  
  .logincontent{
    background: url('images/loginbg.jpg');
    height: calc(100% - 75px);
    background-size: 100% 100%;
  }
  
  .loginfooter{
    background: #637A9F;
    height: 35px;
    color: white;
    line-height: 30px;
    font-size: 10pt;
    text-align: center;
  }
  
  .popup{
    width:100%;
    height: 100%;
    background: rgb(0,0,0, 0.50);
    display: none;
  }
  
  .popupwindow{
      position: absolute;
      left: 50%;
      top: 50%;
      padding: 10px;
      transform: translate(-50%, -50%);
      border-radius: 10px; 
  }
  
  .loginstyle1{
    height: 30px;
    line-height: 30px;
    font-size: 16pt;
    font-weight: bold;
    text-align: center;
  }
  
  .loginstyle2{
    position: relative;
    height: 400px;
    padding: 10px;
    background: whitesmoke;
  }
  
  
  .txtbox{
    width: 98%;
    height: 30px;
    font-size:10pt;
    border: 1px solid silver;
    border-radius: 5px; 
    padding: 8px;
    box-sizing: border-box;
  }
  
  .txtbox1{
    width: 50px;
    height: 40px;
    font-size:10pt;
    border: 1px solid silver;
    border-radius: 5px; 
    padding: 8px;
    box-sizing: border-box;
  }
  
  .btn{
    width: 100%;
    height: 35px;
    background: rgba(232, 129, 3, 0.92);
    color: white;
    border: 1px solid silver;
    border-radius: 10px; 
    cursor: pointer;
  }
  
  .linklabel{
    color: dodgerblue;
    cursor: pointer;
  }

  