.header{
    width: 100%;
    height: 30px;
    background: #637A9F;
    line-height: 30px;
    color: white;
}

.content{
    width: 100%;
    height: calc(100% - 50px);
    background: white;
}

.footer{
    width: 100%;
    height: 20px;
    background: #637A9F;
    color: white;
    font-size: 7pt;
    line-height: 20px;
    text-align: center;
}

.menubar{
    width: 196px;
    padding: 2px;
    height: calc(100% - 4px);
    background: whitesmoke;
    float: left;
}

.outlet{
    width: calc(100% - 200px);
    height: 100%;
    float: left;
}

.menuheader{
    width: 100%;
    height: 30px;
    line-height: 30px;
    background: rgba(232, 129, 3, 0.92);
}

.menuheader img{
    height: 16px;
    float: left;
    padding-left: 5px;
    margin-top: 6px;
    cursor: pointer;
}

.menuheader label{
    color: white;
    font-weight: bold;
    float: left;
    padding-left: 10px;
    cursor: pointer;
}

.menu
{
    width: 100%;
    height: calc(100% - 30px);
    overflow: auto;
}

.mlist{
    width: 100%;
    list-style-type: none;
    margin: 0;
    padding: 0;
    background-color: rgb(232, 200, 114);
}

.mlist li label{
    display: block;
    padding: 8px 5px;
    cursor: pointer;
    background: #BAC6DA;
    border-top: 2px solid white;
}

.mlist li label:hover{
    background: #B6DFE3;
}

.mlist li div{
    display: none;
    width: 100%;
}

.mlist li div label{
    display: block;
    padding: 8px 5px;
    cursor: pointer;
    background: #E9EEF3;
    border-top: 2px solid white;
}

.outlet #titlebar{
    width: 100%;
    height: 30px;
    line-height: 30px;
    font-size: 10pt;
    font-weight: bold;
    background: rgb(232, 200, 114);
}

.outlet #module{
    width: 100%;
    height: calc(100% - 30px);
    border: none;
}
.smenu {
    display: none;
    max-height: 0; 
    overflow: hidden;
    transition: max-height 0.3s ease-in-out;
}

.menu li:hover .smenu {
    display: block;
    max-height: 500px;
}
